$primary: yellow;
$body-bg: hsl(217, 28%, 15%);
$primary: blue;
$cta-bg: linear-gradient(to right, hsl(198, 60%, 50%), hsl(176, 68%, 64%));
$dark: hsl(218, 28%, 13%);
$info: hsl(176, 68%, 64%);
$secondary: hsl(219, 30%, 18%);

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap');
$font-family-sans-serif: 'Raleway', sans-serif;
@import "../node_modules/bootstrap/scss/bootstrap";

.cta {
    background: $cta-bg;
}
.z {
    &-under {
        z-index: -1;
    }
    &-0{
        z-index: 0;
    }
    &-1{
        z-index: 1;
    }
    &-2{
        z-index: 2;
    }
}

.card-img-top {
    height: 65px;
    img {
        height: 100%; 
    }
}
.half-bg { 
    background: linear-gradient(to bottom, #181f2a 50%, hsl(216, 53%, 9%) 50%);
}
.social-item {
    width: 2rem;
    height: 2rem;
}

.rounded-start-pill {
    border-radius: 30px 0 0 30px;
}
.rounded-end-pill {
    border-radius: 0 30px 30px 0;
}